@import url("https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&family=Merienda:wght@400;700&display=swap");

.card-size {
  width: 4vw;
  height: 12vh;
}
.Log {
  width: 30%;
  height: 30%;
  background-color: rgba($color: #ad652a, $alpha: 1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  position: absolute;
  border-radius: 10px;
  top: 30%;
  left: 33%;

  span {
    color: whitesmoke;
    font-size: 1.3em;
    font-family: "Merienda", cursive;
  }

  input {
    min-width: 40%;
    padding: 5px;
  }

  button {
    background-color: royalblue;
    text-transform: uppercase;
    font-size: 1.1em;
    color: seashell;
    font-family: "Merienda", cursive;
    border-radius: 7px;
  }
}
.game-mode {
  max-width: 100%;
  height: 100vh;
  position: relative;

  .green-table {
    background-image: url("../../assets/images/table-cover.jpg");
  }
  .blue-table {
    background-image: url("../../assets/images/blue-table.jpg");
  }
  .red-table {
    background-image: url("../../assets/images/red-table.jpg");
  }

  .game-field {
    border: rgba($color: #2b1b02, $alpha: 1) 9px inset;
    width: 45%;
    height: 70%;
    background-position: center;
    background-size: cover;
    border-radius: 3% 3% 50% 50%;
    position: relative;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -60%);
    display: flex;
    align-items: flex-end;
    justify-content: space-around;

    img {
      position: absolute;
      top: 5%;
      left: 5%;
    }

    .card-place {
      border: whitesmoke 1px solid;
      width: 9.5%;
      height: 15%;
      margin-bottom: 30%;
      border-radius: 2%;
    }
    .card-place:nth-child(1) {
      transform: rotate(70deg);
    }
    .card-place:nth-child(2) {
      margin-bottom: 5%;
    }
    .card-place:nth-child(3) {
      transform: rotate(-70deg);
    }
  }

  button {
    width: 8%;
    height: 6%;
    background-color: rgb(35, 63, 155);
    text-transform: uppercase;
    font-size: 1.4em;
    position: absolute;
    bottom: 10%;
    color: whitesmoke;
    border-radius: 35%;
  }

  button[disabled] {
    background: rgb(99, 97, 97);
  }

  button:nth-child(4) {
    right: 20%;
  }
  button:nth-child(5) {
    right: 10%;
  }
  .restart {
    left: 8%;
    bottom: 20%;
  }

  .player-hand {
    position: absolute;
    bottom: 16%;
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .opponent-hand {
    position: absolute;
    top: 14%;
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .score {
    position: absolute;
    right: 10%;
    bottom: 45%;
    width: 10%;
    label {
      display: flex;
      color: whitesmoke;
      font-size: 1.1em;
      flex-direction: column;
      justify-content: space-between;
      margin-bottom: 10px;
      // margin-left: 3%;

      input {
        margin-top: 5px;
        // width: 3%;
        height: 2%;
        font-size: 1.1em;
        color: darkblue;
      }
    }
  }
  .take-card-animation {
    animation-duration: 0.3s;
    animation-name: takeCard;
  }

  @keyframes takeCard {
    0% {
      transform: translate(0, 0);
    }
    99% {
      transform: translate(417%, 424%);
    }

    100% {
      display: none;
    }
  }

  .opponent-take-card-animation {
    animation-duration: 0.3s;
    animation-name: opponentTakeCard;
  }

  @keyframes opponentTakeCard {
    0% {
      transform: translate(0, 0);
    }
    99% {
      transform: translate(431%, -55%);
    }

    100% {
      display: none;
    }
  }

  .autoplayBtn {
    position: absolute;
    left: 8%;
  }

  .bg-green {
    background: rgb(18, 211, 27);
  }

  .bg-red {
    transform: scale(1.2);
    transition: cubic-bezier(0.075, 0.82, 0.165, 1);
    background: rgb(241, 0, 0);
  }
}

@media (max-width: 1376px) {
  .game-mode .bg-green,
  game-mode .bg-red {
    width: auto;
    font-size: 1.1em;
  }
}
@media (max-width: 992px) {
  .game-mode button:nth-child(5),
  button:nth-child(4) {
    width: auto;
    font-size: 1.1em;
  }

  .game-mode .game-field .card-place {
    display: none;
  }
}

@media (max-width: 800px) {
  .game-mode button:nth-child(5),
  button:nth-child(4) {
    width: auto;
    font-size: 1em;
    margin: 2%;
  }
}

@media (max-width: 700px) {
  .Log input {
    max-width: 70%;
    padding: 0;
  }

  .Log span {
    font-size: 1em;
  }

  .game-mode button:nth-child(5),
  button:nth-child(4) {
    width: auto;
    font-size: 1em;
  }

  .game-mode button:nth-child(5) {
    bottom: 20%;
  }

  .game-mode button:nth-child(4) {
    right: 13%;
  }
}
