.MainMenu{max-width:100%;height:100vh;position:relative}.MainMenu .menu{width:50%;height:60%;background-image:url(/react-game/static/media/menu-bg.e8db4323.jpg);background-size:cover;background-position:center;border-radius:30px;position:absolute;top:50%;left:50%;transform:translate(-50%, -60%);display:flex;flex-direction:column;align-items:center;justify-content:space-evenly}.MainMenu .menu button{font-family:"Merienda", cursive;font-size:1.55em;min-width:30%;height:12%;background:#2f640c;border-radius:35px;color:#ffd9f9}.MainMenu .menu button:hover{background:#00a71c}@media (max-width: 1000px){.MainMenu .menu button{font-size:1em}}@media (max-width: 700px){.MainMenu .menu button{font-size:0.7em}}

.MenuBtn{position:absolute;left:3%;top:3%;font-family:"Akaya Kanadaka", cursive;font-size:1.1em;border-radius:5px;background:linear-gradient(#26361d, #420d18);color:whitesmoke;z-index:2}

.card-size{width:4vw;height:12vh}.Log{width:30%;height:30%;background-color:#ad652a;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;position:absolute;border-radius:10px;top:30%;left:33%}.Log span{color:whitesmoke;font-size:1.3em;font-family:"Merienda", cursive}.Log input{min-width:40%;padding:5px}.Log button{background-color:royalblue;text-transform:uppercase;font-size:1.1em;color:seashell;font-family:"Merienda", cursive;border-radius:7px}.game-mode{max-width:100%;height:100vh;position:relative}.game-mode .green-table{background-image:url(/react-game/static/media/table-cover.78bc963d.jpg)}.game-mode .blue-table{background-image:url(/react-game/static/media/blue-table.b8151039.jpg)}.game-mode .red-table{background-image:url(/react-game/static/media/red-table.0b53a54c.jpg)}.game-mode .game-field{border:#2b1b02 9px inset;width:45%;height:70%;background-position:center;background-size:cover;border-radius:3% 3% 50% 50%;position:relative;top:60%;left:50%;transform:translate(-50%, -60%);display:flex;align-items:flex-end;justify-content:space-around}.game-mode .game-field img{position:absolute;top:5%;left:5%}.game-mode .game-field .card-place{border:whitesmoke 1px solid;width:9.5%;height:15%;margin-bottom:30%;border-radius:2%}.game-mode .game-field .card-place:nth-child(1){transform:rotate(70deg)}.game-mode .game-field .card-place:nth-child(2){margin-bottom:5%}.game-mode .game-field .card-place:nth-child(3){transform:rotate(-70deg)}.game-mode button{width:8%;height:6%;background-color:#233f9b;text-transform:uppercase;font-size:1.4em;position:absolute;bottom:10%;color:whitesmoke;border-radius:35%}.game-mode button[disabled]{background:#636161}.game-mode button:nth-child(4){right:20%}.game-mode button:nth-child(5){right:10%}.game-mode .restart{left:8%;bottom:20%}.game-mode .player-hand{position:absolute;bottom:16%;display:flex;justify-content:center;width:100%}.game-mode .opponent-hand{position:absolute;top:14%;display:flex;justify-content:center;width:100%}.game-mode .score{position:absolute;right:10%;bottom:45%;width:10%}.game-mode .score label{display:flex;color:whitesmoke;font-size:1.1em;flex-direction:column;justify-content:space-between;margin-bottom:10px}.game-mode .score label input{margin-top:5px;height:2%;font-size:1.1em;color:darkblue}.game-mode .take-card-animation{animation-duration:0.3s;animation-name:takeCard}@keyframes takeCard{0%{transform:translate(0, 0)}99%{transform:translate(417%, 424%)}100%{display:none}}.game-mode .opponent-take-card-animation{animation-duration:0.3s;animation-name:opponentTakeCard}@keyframes opponentTakeCard{0%{transform:translate(0, 0)}99%{transform:translate(431%, -55%)}100%{display:none}}.game-mode .autoplayBtn{position:absolute;left:8%}.game-mode .bg-green{background:#12d31b}.game-mode .bg-red{transform:scale(1.2);transition:cubic-bezier(0.075, 0.82, 0.165, 1);background:#f10000}@media (max-width: 1376px){.game-mode .bg-green,game-mode .bg-red{width:auto;font-size:1.1em}}@media (max-width: 992px){.game-mode button:nth-child(5),button:nth-child(4){width:auto;font-size:1.1em}.game-mode .game-field .card-place{display:none}}@media (max-width: 800px){.game-mode button:nth-child(5),button:nth-child(4){width:auto;font-size:1em;margin:2%}}@media (max-width: 700px){.Log input{max-width:70%;padding:0}.Log span{font-size:1em}.game-mode button:nth-child(5),button:nth-child(4){width:auto;font-size:1em}.game-mode button:nth-child(5){bottom:20%}.game-mode button:nth-child(4){right:13%}}

.settings-page{max-width:100%;height:100vh;position:relative}.settings-page .settings-window{width:50%;height:60%;background-image:url(/react-game/static/media/setting-bg.9d2a2d8b.PNG);background-size:cover;background-position:center;position:absolute;top:50%;left:50%;transform:translate(-50%, -60%);display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;border-radius:30px;font-family:sans-serif;text-align:center}.settings-page .settings-window label,.settings-page .settings-window span,.settings-page .settings-window button{color:whitesmoke;font-family:"Akaya Kanadaka", cursive;font-size:1.4em;margin-right:20px}.settings-page .settings-window button{padding:1%;border-radius:20px;background:linear-gradient(#4f882e, #dd0732)}.settings-page .settings-window select{padding:10px;font-size:1.2em;background:cornflowerblue;font-family:"Akaya Kanadaka";color:#470505}@media (max-width: 700px){.settings-page .settings-window select{padding:5px;font-size:1em}.settings-page .settings-window select option{font-size:1em;max-height:1em}}

.music{right:9%;top:3%;position:absolute;font-family:"Akaya Kanadaka", cursive;font-size:1.1em;border-radius:5px;background:linear-gradient(#232f44, #381219);color:whitesmoke;z-index:2}.sound{right:3%;top:3%;position:absolute;font-family:"Akaya Kanadaka", cursive;font-size:1.1em;border-radius:5px;background:linear-gradient(#232f44, #381219);color:whitesmoke;z-index:2}@media (max-width: 1400px){.music{right:12%}}@media (max-width: 992px){.music{right:15%;font-size:1em}.sound{font-size:1em}}@media (max-width: 700px){.music{right:20%}}

.rules{max-width:100%;height:100vh;position:relative}.rules span{color:whitesmoke}.rules .rules-window{width:50%;height:60%;background-image:url(/react-game/static/media/setting-bg.9d2a2d8b.PNG);background-size:cover;background-position:center;position:absolute;top:50%;left:50%;transform:translate(-50%, -60%);flex-direction:column;align-items:center;justify-content:space-evenly;border-radius:30px;font-family:sans-serif;text-align:center}.rules .rules-window .hot-keys{position:absolute;left:35%;background:rgba(0,0,0,0.1);display:flex;flex-direction:column;justify-content:space-evenly;align-items:flex-start;width:90%;height:50%}.rules .rules-window .hot-keys span{font-size:1.4em;transform:translateX(-12%);font-family:"Akaya Kanadaka", cursive}.rules .rules-window .hot-keys div{display:flex;justify-content:space-evenly;color:whitesmoke;font-size:1.2em;font-family:"Akaya Kanadaka", cursive}.rules .rules-window .hot-keys .keys{font-size:1.2em;color:red;font-weight:700;font-size:1.4em;margin-right:20px;transform:translateY(-20%)}.rules .rules-window .how-play{font-family:"Akaya Kanadaka", cursive;width:100%;height:50%;position:absolute;bottom:0;display:flex}.rules .rules-window .how-play span:first-child{align-self:center;font-size:1.4em;width:30%;margin-right:5%;margin-left:5%}.rules .rules-window .how-play .cards-rules{display:flex;flex-basis:80%;justify-content:space-around;flex-wrap:wrap;flex-direction:column}.rules .rules-window .how-play .cards-rules div{display:flex;align-items:center}.rules .rules-window .how-play .cards-rules div img{margin-right:3px}.rules .rules-window .how-play .rules-points{font-size:1.2em}.rules .rules-window .how-play .card-for-rules{width:40px}@media (max-width: 1376px){.rules .rules-window .how-play .card-for-rules{width:30px}.rules .rules-window .how-play .rules-points,.rules .rules-window .hot-keys div,.rules .rules-window .hot-keys span{font-size:1em}}@media (max-width: 992px){.rules .rules-window .how-play .card-for-rules{width:30px}.rules .rules-window .how-play .rules-points,.rules .rules-window .hot-keys div{font-size:1em}.rules .rules-window .how-play .cards-rules{display:none}.rules .rules-window .how-play span:first-child{width:85%}.rules .rules-window .hot-keys{top:3%;left:25%}}@media (max-width: 700px){.rules .rules-window .hot-keys{top:3%;left:15%}}

.stats-page{max-width:100%;height:100vh;position:relative}.stats-page table{margin:10% 10%;color:whitesmoke;width:100%;text-align:center;font-family:"Akaya Kanadaka", cursive;font-size:1.3em}.stats-page .stats-window{width:50%;height:60%;background-image:url(/react-game/static/media/setting-bg.9d2a2d8b.PNG);background-size:cover;background-position:center;position:absolute;top:50%;left:50%;transform:translate(-50%, -60%);font-family:sans-serif;border-radius:30px}@media (max-width: 1200px){.stats-page table{font-size:1em;margin:5%}}@media (max-width: 992px){.stats-page table{font-size:0.7em;margin:0}}

#root,.fullscreen-enabled .my-component,.fullscreen{background-image:url(/react-game/static/media/background.41eb82c9.png);background-position:center;background-size:cover;max-width:100%;height:100vh}button:hover{transform:scale(1.1);transition:transform 0.2s ease-in-out;cursor:pointer}footer{position:fixed;height:30px}.rs-logo{position:fixed;bottom:3%;left:50%;height:6%;background:rgba(56,40,40,0.3)}.my-git{position:fixed;bottom:3%;left:40%;height:6%}

